ion-content {
  --ion-background-color: var(--ion-color-dark);
}

.ion-page {
  background-color: var(--ion-color-black);
}

ion-toolbar {
  --min-height: 8vh !important;
}

ion-header {
  position: sticky;
  top: 0;
}

ion-menu > * {
  background-color: var(--ion-menu-background);
}

ion-label {
  color: white !important;
  font-size: 1.25rem !important;
}

ion-item-divider {
  margin-bottom: 10px;
}

.footer {
  position: sticky;
  bottom: 0;
  left: 0;
  width: 100%;
}

.btn {
  &-sm {
    height: 45px !important;
    width: 45px !important;
  }

  &-md {
    height: 60px !important;
    width: 60px !important;
  }

  &-lg {
    height: 65px !important;
    width: 65px !important;
  }
}

.nav-icon {
  &-sm {
    font-size: 1.5rem;
  }

  &-md {
    font-size: 2.5rem;
  }

  &-lg {
    font-size: 3.5rem;
  }
}

.toolbar-container {
  min-height: unset !important;
  height: 75px !important;
}

.page-content {
  max-width: 550px;
  width: 95%;
  padding: 12.5px 0px;
}

.with-header-offset {
  --offset-top: -90px !important;
  --offset-bottom: 0px !important;
}

.w-100 {
  width: 100%;
}

.flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.row,
.col {
  &-start {
    justify-content: flex-start;
  }

  &-center {
    justify-content: center;
  }

  &-end {
    justify-content: flex-end;
  }
}
