.plate-image {
  display: flex;
  align-self: center;
  object-fit: cover;
  width: 100%;
  aspect-ratio: 1/1;
  border: 8px solid #ffffff;
  border-radius: 20px;
  overflow: hidden;
}

.plate-title {
  font-size: 1.5rem;
  white-space: none;
  margin-bottom: 10px;
}

.datetime-disabled {
  color: #8b8b8b;
  opacity: 1;
}
