.card-title {
  margin-left: 10px;
  padding-top: 10px;
}

.card-modal {
  --width: 85% !important;
  --height: 75% !important;
  --background: #43474f !important;
  --border-radius: 10px !important;
}

ion-modal > .modal-wrapper {
  box-shadow: var(--component-shadow) !important;
  -webkit-box-shadow: var(--component-shadow) !important;
  -moz-box-shadow: var(--component-shadow) !important;
  max-width: 450px !important;
}
