.calendar {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.calendar > .react-calendar {
  border-radius: 20px;
  border: none;
  overflow: hidden;
  width: 85% !important;
  background-color: var(--ion-color-dark);
  color: white;
}

.react-calendar__tile--now {
  background: none;
  border-radius: 8px !important;
  overflow: hidden;
  color: var(--ion-color-primary) !important;
  font-weight: bold;
}

.react-calendar__navigation {
  margin: 0px !important;
}

.react-calendar__navigation__label,
.react-calendar__navigation__arrow {
  color: #ffffff !important;
  text-transform: uppercase;
  font-size: 0.785rem;
  font-weight: bold;
}

.react-calendar__navigation__arrow {
  font-size: x-large;
  transform: translateY(-3px);
  font-weight: normal;
}

.react-calendar__navigation:enabled:hover,
.react-calendar__navigation__label:enabled:hover,
.react-calendar__navigation__arrow:enabled:hover,
.react-calendar__navigation:enabled:focus,
.react-calendar__navigation__label:enabled:focus,
.react-calendar__navigation__arrow:enabled:focus {
  color: #202020 !important;
  background-color: #ffffffb0 !important;
}

.react-calendar__tile:enabled:hover {
  border-radius: 8px !important;
  overflow: hidden;
  background-color: #ffffffb0;
  color: black !important;
}

.react-calendar__month-view__days__day--weekend
  + .react-calendar__tile:enabled:hover.react-calendar__month-view__days__day--weekend {
  border-radius: 8px !important;
  overflow: hidden;
  background-color: #ffffffb0;
  color: #c52626 !important;
}

.react-calendar__tile--active,
.react-calendar__tile--hasActive,
.react-calendar__tile:enabled:focus {
  border-radius: 8px !important;
  overflow: hidden;
  background-color: white;
  color: black !important;
  font-weight: bold;

  box-shadow: var(--component-shadow);
  -webkit-box-shadow: var(--component-shadow);
  -moz-box-shadow: var(--component-shadow);
}

.react-calendar__month-view__days__day--weekend
  + .react-calendar__tile:enabled:focus.react-calendar__month-view__days__day--weekend {
  border-radius: 8px !important;
  overflow: hidden;
  background-color: white;
  color: #c52626 !important;

  box-shadow: var(--component-shadow);
  -webkit-box-shadow: var(--component-shadow);
  -moz-box-shadow: var(--component-shadow);
}

.react-calendar__month-view__days__day {
  color: white;
  aspect-ratio: 1/1;
}

.react-calendar__month-view__days__day--weekend
  + .react-calendar__month-view__days__day--weekend {
  color: rgb(238, 82, 82);
}

.react-calendar__month-view__days__day--neighboringMonth {
  color: #555555;
}

.react-calendar__decade-view__years__year.react-calendar__tile {
  padding: 10px 0 !important;
  flex-basis: 27.5% !important;
  color: white;
}

.react-calendar__decade-view {
  display: flex;
  flex-direction: column;
  align-content: center;
  padding-top: 65px;
}

.react-calendar__decade-view__years {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.react-calendar__viewContainer {
  padding: 0 10px 10px;
  height: 295px !important;
}

abbr[title] {
  border-bottom: none !important;
  cursor: inherit !important;
  text-decoration: none !important;
  font-size: 10px;
}
